.contents_background_img {
    position: fixed;
    top: 0%;
    left: 0%;
    width: auto;
    height: 120%;
}

@media (prefers-reduced-motion: no-preference) {
    .contents_background_img {
      animation: App-logo-spin infinite 3s linear;
    }
}

@keyframes App-logo-spin {
    from {
      transform: translateX(-18%);
      /* transform: translate(-18%, -6%); */
    }
    to {
      transform: translate(0%);
    }
  }
  
  /* quest board */
  .questboard_root {
      position: relative;
      width: 100%;
      height: 100%;
  }

  .questboard_grid {
    position: relative;
      width: 100%;
      height: 29%;
      margin-left: 3%;
      margin-right: 3%;
      margin-top: 3%;
      margin-bottom: 6%;
      display: flex;
      overflow: hidden;
  }
  
  .questboard_grid_card {
    position: relative;
    height: 96%;
    background-color: #189DB7;
    margin-right: 20px; 
    margin-left: 1px; 
    margin-top: 1px; 
    margin-bottom: 1px; 
    outline: 1px solid #BBBBBB;
    outline-offset: 0px;
    box-shadow: 6px 6px 0px 0px rgba(0,0,0,0.75);
  }

  .questboard_grid_card_image {
    position: relative;
    height: 53%;
    margin-left: auto;
    margin-right: auto;
    width: 110px;
    background-color: red;
  }

  .questboard_grid_card_title {
    position: absolute;
    top: 53%;
    width: 100%;
    left: 0%;
    padding-top: 1px;
    padding-bottom: 2px;
    font-size: 65%;
    font-weight: bold;
    text-align: center;
    vertical-align: center;
    color: white;
    background-color: green;
  }

  .questboard_grid_card_link_info {
    position: absolute;
    top: 64%;
    width: 100%;
    left: 0%;
    font-size: 70%;
    text-align: center;
    color: white;
  }

   .questboard_grid_card_link_icon {
    position: absolute;
    width: 14%;
    left: 25%;
    
  }

  .questboard_grid_card_link_text {
    position: absolute;
    left: 40%;
    border-radius: 4px;
    background: gray;
    padding: 2px;
  }

  .questboard_grid_card_stat_info {
    position: absolute;
    top: 75%;
    width: 100%;
    height: 19%;
    left: 0%;
    font-size: 90%;
    text-align: center;
    color: white;
  }

  .questboard_grid_card_stat_icon {
    position: absolute;
    height: 100%;
    left: 20%;

  }

  .questboard_grid_card_stat_text {
    position: absolute;
    top: 20%;
    left: 50%;
  }


  /* my quest */
  .myquest_root {
    position: absolute;
    display: flex;
    width: 100%;
    height: 98%;
    margin: 0%;
}

.myquest_grid_container {
    position: relative;
    width: 100%;
    height: 100%;
}

.myquest_grid_card {
  position: relative;
  left: 3%;
  width: 93%;
  height: 27.5%;
  background-color: #189DB7;
  margin-right: 20px; 
  margin-left: 2px; 
  margin-top: 10px; 
  margin-bottom: 36px;
  outline: 1px solid #BBBBBB;
  outline-offset: 0px;
  box-shadow: 6px 6px 0px 0px rgba(0,0,0,0.75);
}

.myquest_grid_card_title {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 97%;
  height: 18%;
  background-color: #68aDB7;
  padding-left: 3%; 
  margin-bottom: 2px;
  color: white;
  font-size: 130%;
  font-weight: bold;
  text-align: left;
}

.myquest_grid_card_title_link_img {
  position: absolute;
  top: 1%;
  right: 15%;
  height: 15%;
}

.myquest_grid_card_title_link {
  position: absolute;
  top: 2%;
  right: 3%;
  border-radius: 4px;
  background: gray;
  padding: 2px;
}

.myquest_grid_card_empty_text {
  position: absolute;
  top: 50%;
  left: 0%;
  width: 100%;
  height: 15%;
  text-align: center;
  font-size: 130%;
  color: white;
}

.myquest_grid_card_title_img {
  position: absolute;
  top: 1%;
  right: 15%;
  height: 15%;
}

.myquest_grid_card_quest_image {
  position: absolute;
  top: 18%;
  height: 80%;
  left: 2%;
  width: 35%;
}

.myquest_grid_card_quest_text {
  position: absolute;
  top: 35%;
  left: 37%;
  height: 20%;
  width: 63%;
  font-size: 150%;
  font-weight: bold;
  color: white;
}

.myquest_grid_card_stat_info {
  position: absolute;
  top: 60%;
  width: 63%;
  height: 25%;
  left: 37%;
  font-size: 130%;
  text-align: center;
  color: white;
}

.myquest_grid_card_stat_icon {
  position: absolute;
  height: 100%;
  left: 35%;
}

.myquest_grid_card_stat_text {
  position: absolute;
  top: 20%;
  left: 60%;
}

.questhistory_content_body_text {
  position: absolute;
  top: 50%;
  left: 0%;
  width: 100%;
  height: 100%;
  color: white;
  font-size: 150%;
  font-weight: bold;
}

.questhistory_grid_card_container {
  position: relative;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
}

.questhistory_grid_card {
  position: relative;
  left: 3%;
  width: 20%;
  height: 20%;
  background-color: #189DB7;
  margin-right: 20px; 
  margin-left: 2px; 
  margin-top: 10px; 
  margin-bottom: 36px;
  outline: 1px solid #BBBBBB;
  outline-offset: 0px;
  box-shadow: 6px 6px 0px 0px rgba(0,0,0,0.75);
}
.questhistory_grid_card_img {
  position: absolute;
  height: 80%;
