.navigationbar_stat_root {
    position: relative;
    width: 100%;
    height: 50%;
}

.navigationbar_class_icon {
    position: absolute;
    top: -15%;
    right: 13%;
    width: 12%;
    z-index: 1;
}

.navigationbar_root {
    position: relative;
    width: 98%;
    height: 86%;
    margin: 1%;
}

.navigationbar_action_icon {
    width: 10%;
    height: auto;
    margin-top: 3%;
    margin-left: 5%;
    margin-right: 5%;
}

.navigationbar_stat_level_label {
    position: absolute;
    top: -50%;
    right: 3%;
    width: 100%;
    height: 100%;
    z-index: 1;
    text-align: right;
    font-size: 70%;
    color: white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
